import React, { Component } from 'react';

let data = [
    { key:"1", empresa: "PrFrio Refrigerao Industrial", cargo: "Desenhista Projetista", inicio: "Outubro/2012", fim: "agora", descricao: "Desenho e projeto de cmaras frigorficas, encaminhamento de eletrocalhas e eletrodutos (eltrica), arranjo da cmara frigorfica com painis de EPS, XPS ou PUR (civil), projeto dimensional de racks, chillers ou unidades condensadoras no Solidworks ou AutoCAD (disposio de equipamentos, encaminhamento de tubulao e projeto de estrutura ou carenagem); dimensionamento de tubulao da unidade condensadora at o evaporador; posicionamento dos componentes e encaminhamento da tubulao; quantificao de materiais a serem usados na execuo dos projetos, tanto de racks quanto a cmara frigorfica em si; criao de projetos e gerenciamento de materiais no sistema Protheus (Microsiga); visitas tcnicas para detalhamento dos projetos; elaborao de manual tcnico da instalao para o cliente, gerenciamento da rea de informtica, fazendo a manuteno dos computadores em caso de falha, criao e manuteno de e-mails da empresa, gerenciamento do site." },
    { key:"2", empresa: "Indstria Mecnica Favimar", cargo: "Meio Oficial Ferramenteiro", inicio: "Abril/2011", fim: "Setembro/2012", descricao: "Usinagem de estampos, desde a pea bruta at o try-out do produto, envolvendo usinagem em tornos, fresas e eletro-eroso a fio. Manuteno em estampos e auxlio na produo da empresa." },
    { key:"3", empresa: "Grupo Bluestar Net", cargo: "Auxiliar Geral", inicio: "Outubro/2010", fim: "Janeiro/2011", descricao: "Usinagem de moldes plsticos, desde a pea bruta at o try-out do produto." },
    { key:"4", empresa: "Clube Esperia", cargo: "Aprendiz da rea Financeira", inicio: "Julho/2010", fim: "Outubro/2010", descricao: "Emisso de boletos, baixa em cheques, contato com clientes sobre cheques devolvidos, etc." },
    { key:"5", empresa: "SABESP - Companhia de Saneamento Bsico do estado de So Paulo", cargo: "Estagirio em Tcnico em Mecnica", inicio: "Abril/2009", fim: "Dezembro/2009", descricao: "Conhecimento sobre o sistema hdrico de abastecimento da populao. Detalhamento de bombas hidrulicas e vlvulas. Envolvimento com o sistema de qualidade da empresa, contatando gerentes e supervisores de outras unidades sobre o servio prestado da empresa. Desenho utilizando o AutoCAD sobre eixos de bombas, dispositivos de iamento, mancais de deslizamento." },
];

export default class Experiencia extends Component {
    render() {
        return(
            <div>
                <div>
                    <h2>Experincia</h2>
                </div>
                <div>
                    {data.map ((data) =>
                        <div key="{data.key}">
                            <h3>{ data.empresa }</h3>
                            <h4>{ data.cargo }</h4>
                            <div><strong>{ data.inicio }</strong> at <strong>{ data.fim }</strong></div>
                            <p>{ data.descricao }</p>
                            <hr />
                        </div>
                    )};
                </div>
            </div>
        );
    }
};



